"use client";

import { useMemo } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Lightbulb, AlertCircle, CheckCircle } from "lucide-react";

interface PasswordSuggestionsProps {
  password: string;
}

interface Suggestion {
  type: "error" | "warning" | "success";
  message: string;
  icon: React.ReactNode;
}

export function PasswordSuggestions({ password }: PasswordSuggestionsProps) {
  const suggestions = useMemo(() => {
    try {
      if (!password) {
        return [];
      }

      const suggestions: Suggestion[] = [];

      // Length checks
      if (password.length < 8) {
        suggestions.push({
          type: "error",
          message: "Use at least 8 characters for better security",
          icon: <AlertCircle className="h-4 w-4" />
        });
      } else if (password.length < 12) {
        suggestions.push({
          type: "warning",
          message: "Consider using 12+ characters for stronger security",
          icon: <Lightbulb className="h-4 w-4" />
        });
      } else {
        suggestions.push({
          type: "success",
          message: "Good password length",
          icon: <CheckCircle className="h-4 w-4" />
        });
      }

      // Character variety checks
      const hasLower = /[a-z]/.test(password);
      const hasUpper = /[A-Z]/.test(password);
      const hasNumbers = /\d/.test(password);
      const hasSymbols = /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/.test(password);

      if (!hasLower) {
        suggestions.push({
          type: "error",
          message: "Add lowercase letters (a-z)",
          icon: <AlertCircle className="h-4 w-4" />
        });
      }

      if (!hasUpper) {
        suggestions.push({
          type: "error",
          message: "Add uppercase letters (A-Z)",
          icon: <AlertCircle className="h-4 w-4" />
        });
      }

      if (!hasNumbers) {
        suggestions.push({
          type: "error",
          message: "Add numbers (0-9)",
          icon: <AlertCircle className="h-4 w-4" />
        });
      }

      if (!hasSymbols) {
        suggestions.push({
          type: "error",
          message: "Add special characters (!@#$%^&*)",
          icon: <AlertCircle className="h-4 w-4" />
        });
      }

      // Check for common patterns and dictionary words
      const commonPatterns = [
        { pattern: /123456/, message: "Avoid sequential numbers like '123456'" },
        { pattern: /password/i, message: "Don't use the word 'password'" },
        { pattern: /qwerty/i, message: "Avoid keyboard patterns like 'qwerty'" },
        { pattern: /abc123/i, message: "Avoid simple combinations like 'abc123'" },
        { pattern: /admin/i, message: "Don't use common words like 'admin'" },
        { pattern: /letmein/i, message: "Avoid phrases like 'letmein'" },
        { pattern: /welcome/i, message: "Don't use common words like 'welcome'" },
        { pattern: /monkey/i, message: "Avoid common words like 'monkey'" },
        { pattern: /dragon/i, message: "Don't use common words like 'dragon'" },
        { pattern: /master/i, message: "Avoid common words like 'master'" },
        { pattern: /login/i, message: "Don't use words like 'login'" },
        { pattern: /user/i, message: "Avoid generic words like 'user'" },
        { pattern: /test/i, message: "Don't use words like 'test'" },
        { pattern: /guest/i, message: "Avoid words like 'guest'" },
        { pattern: /root/i, message: "Don't use system words like 'root'" }
      ];

      for (const { pattern, message } of commonPatterns) {
        if (pattern.test(password)) {
          suggestions.push({
            type: "error",
            message,
            icon: <AlertCircle className="h-4 w-4" />
          });
        }
      }

      // Check for sequential characters
      const sequentialPatterns = [
        /(?:abc|bcd|cde|def|efg|fgh|ghi|hij|ijk|jkl|klm|lmn|mno|nop|opq|pqr|qrs|rst|stu|tuv|uvw|vwx|wxy|xyz)/i,
        /(?:012|123|234|345|456|567|678|789)/,
        /(?:987|876|765|654|543|432|321|210)/
      ];

      for (const pattern of sequentialPatterns) {
        if (pattern.test(password)) {
          suggestions.push({
            type: "warning",
            message: "Avoid sequential characters (abc, 123, etc.)",
            icon: <Lightbulb className="h-4 w-4" />
          });
          break;
        }
      }

      // Check for repeated characters
      if (/(.)\1{2,}/.test(password)) {
        suggestions.push({
          type: "warning",
          message: "Avoid repeating the same character multiple times",
          icon: <Lightbulb className="h-4 w-4" />
        });
      }

      // Check for keyboard patterns
      const keyboardPatterns = [
        /qwertyuiop/i,
        /asdfghjkl/i,
        /zxcvbnm/i,
        /qazwsx/i,
        /wsxedc/i,
        /rfvtgb/i
      ];

      for (const pattern of keyboardPatterns) {
        if (pattern.test(password)) {
          suggestions.push({
            type: "warning",
            message: "Avoid keyboard patterns (qwerty, asdf, etc.)",
            icon: <Lightbulb className="h-4 w-4" />
          });
          break;
        }
      }

      // Check for common substitutions
      const hasCommonSubstitutions = /[@4aA][sS5$][sS5$]|[0oO][sS5$][sS5$]|[1iI!][sS5$][sS5$]/i.test(password);
      if (hasCommonSubstitutions) {
        suggestions.push({
          type: "warning",
          message: "Avoid predictable character substitutions (@ for a, $ for s)",
          icon: <Lightbulb className="h-4 w-4" />
        });
      }

      // Check for dates
      if (/19\d{2}|20\d{2}/.test(password)) {
        suggestions.push({
          type: "warning",
          message: "Avoid using years or dates in passwords",
          icon: <Lightbulb className="h-4 w-4" />
        });
      }

      // Positive feedback for good practices
      if (hasLower && hasUpper && hasNumbers && hasSymbols && password.length >= 12) {
        suggestions.push({
          type: "success",
          message: "Excellent character variety!",
          icon: <CheckCircle className="h-4 w-4" />
        });
      }

      // Additional security tips
      if (suggestions.filter(s => s.type === "error").length === 0) {
        suggestions.push({
          type: "success",
          message: "Consider using a passphrase with random words",
          icon: <Lightbulb className="h-4 w-4" />
        });
      }

      return suggestions;
    } catch (error) {
      console.error("Error generating password suggestions:", error);
      return [{
        type: "error" as const,
        message: "Unable to analyze password",
        icon: <AlertCircle className="h-4 w-4" />
      }];
    }
  }, [password]);

  if (!password || suggestions.length === 0) {
    return null;
  }

  const errorCount = suggestions.filter(s => s.type === "error").length;
  const warningCount = suggestions.filter(s => s.type === "warning").length;
  const successCount = suggestions.filter(s => s.type === "success").length;

  return (
    <Card className="border-0 shadow-lg">
      <CardHeader className="pb-3">
        <div className="flex items-center justify-between">
          <CardTitle className="flex items-center space-x-2 text-lg">
            <Lightbulb className="h-5 w-5 text-yellow-500" />
            <span>Security Recommendations</span>
          </CardTitle>
          <div className="flex space-x-2">
            {errorCount > 0 && (
              <Badge variant="destructive" className="text-xs">
                {errorCount} Critical
              </Badge>
            )}
            {warningCount > 0 && (
              <Badge variant="secondary" className="text-xs bg-yellow-100 text-yellow-800">
                {warningCount} Warning{warningCount > 1 ? 's' : ''}
              </Badge>
            )}
            {successCount > 0 && (
              <Badge variant="secondary" className="text-xs bg-green-100 text-green-800">
                {successCount} Good
              </Badge>
            )}
          </div>
        </div>
      </CardHeader>
      <CardContent>
        <div className="space-y-3">
          {suggestions.map((suggestion, index) => (
            <div
              key={index}
              className={`flex items-start space-x-3 p-3 rounded-lg transition-all duration-200 ${
                suggestion.type === "error"
                  ? "bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800"
                  : suggestion.type === "warning"
                  ? "bg-yellow-50 dark:bg-yellow-900/20 border border-yellow-200 dark:border-yellow-800"
                  : "bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800"
              }`}
            >
              <div
                className={`mt-0.5 ${
                  suggestion.type === "error"
                    ? "text-red-500"
                    : suggestion.type === "warning"
                    ? "text-yellow-500"
                    : "text-green-500"
                }`}
              >
                {suggestion.icon}
              </div>
              <p
                className={`text-sm ${
                  suggestion.type === "error"
                    ? "text-red-700 dark:text-red-300"
                    : suggestion.type === "warning"
                    ? "text-yellow-700 dark:text-yellow-300"
                    : "text-green-700 dark:text-green-300"
                }`}
              >
                {suggestion.message}
              </p>
            </div>
          ))}
        </div>
      </CardContent>
    </Card>
  );
}
