/**
 * Utility functions for checking passwords against the Have I Been Pwned database
 * Uses the k-anonymity model to protect user privacy
 */

/**
 * Calculate SHA-1 hash of a string using Web Crypto API
 */
async function sha1(message: string): Promise<string> {
  try {
    const msgBuffer = new TextEncoder().encode(message);
    const hashBuffer = await crypto.subtle.digest('SHA-1', msgBuffer);
    const hashArray = Array.from(new Uint8Array(hashBuffer));
    const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
    return hashHex.toUpperCase();
  } catch (error) {
    console.error('Error calculating SHA-1 hash:', error);
    throw new Error('Failed to calculate hash');
  }
}

/**
 * Check if a password has been found in data breaches using the Have I Been Pwned API
 * @param password - The password to check
 * @returns Promise<boolean> - true if password found in breaches, false otherwise
 */
export async function checkPwned(password: string): Promise<boolean> {
  try {
    if (!password || password.length === 0) {
      return false;
    }

    // Calculate SHA-1 hash of the password
    const hash = await sha1(password);
    
    // Take first 5 characters as prefix for k-anonymity
    const prefix = hash.substring(0, 5);
    const suffix = hash.substring(5);

    // Make API request to Have I Been Pwned
    const response = await fetch(`https://api.pwnedpasswords.com/range/${prefix}`, {
      method: 'GET',
      headers: {
        'User-Agent': 'SecurePass-Analyzer'
      }
    });

    if (!response.ok) {
      if (response.status === 429) {
        throw new Error('Rate limit exceeded. Please try again later.');
      }
      throw new Error(`API request failed with status: ${response.status}`);
    }

    const data = await response.text();
    
    // Parse response to check if our hash suffix exists
    const lines = data.split('\n');
    for (const line of lines) {
      const [hashSuffix, count] = line.trim().split(':');
      if (hashSuffix === suffix) {
        return true; // Password found in breaches
      }
    }

    return false; // Password not found in breaches
  } catch (error) {
    console.error('Error checking pwned password:', error);
    // Return null or false on error - don't fail the entire check
    // In a production app, you might want to show a warning to the user
    throw error;
  }
}

/**
 * Get the number of times a password has been seen in breaches
 * @param password - The password to check
 * @returns Promise<number> - Number of times seen in breaches, 0 if not found
 */
export async function getPwnedCount(password: string): Promise<number> {
  try {
    if (!password || password.length === 0) {
      return 0;
    }

    const hash = await sha1(password);
    const prefix = hash.substring(0, 5);
    const suffix = hash.substring(5);

    const response = await fetch(`https://api.pwnedpasswords.com/range/${prefix}`, {
      method: 'GET',
      headers: {
        'User-Agent': 'SecurePass-Analyzer'
      }
    });

    if (!response.ok) {
      throw new Error(`API request failed with status: ${response.status}`);
    }

    const data = await response.text();
    const lines = data.split('\n');
    
    for (const line of lines) {
      const [hashSuffix, count] = line.trim().split(':');
      if (hashSuffix === suffix) {
        return parseInt(count, 10);
      }
    }

    return 0;
  } catch (error) {
    console.error('Error getting pwned count:', error);
    throw error;
  }
}
