"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Slider } from "@/components/ui/slider";
import { Switch } from "@/components/ui/switch";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { RefreshCw, Copy, Check, Wand2 } from "lucide-react";
import { toast } from "sonner";

interface PasswordGeneratorProps {
  onPasswordGenerated: (password: string) => void;
}

interface GeneratorSettings {
  length: number;
  includeUppercase: boolean;
  includeLowercase: boolean;
  includeNumbers: boolean;
  includeSymbols: boolean;
  excludeSimilar: boolean;
  excludeAmbiguous: boolean;
}

export function PasswordGenerator({ onPasswordGenerated }: PasswordGeneratorProps) {
  const [generatedPassword, setGeneratedPassword] = useState("");
  const [copied, setCopied] = useState(false);
  const [isGenerating, setIsGenerating] = useState(false);
  const [settings, setSettings] = useState<GeneratorSettings>({
    length: 16,
    includeUppercase: true,
    includeLowercase: true,
    includeNumbers: true,
    includeSymbols: true,
    excludeSimilar: true,
    excludeAmbiguous: false
  });

  const generatePassword = async () => {
    try {
      setIsGenerating(true);

      // Validate settings
      if (!settings.includeUppercase && !settings.includeLowercase && 
          !settings.includeNumbers && !settings.includeSymbols) {
        toast.error("Please select at least one character type");
        return;
      }

      if (settings.length < 4) {
        toast.error("Password length must be at least 4 characters");
        return;
      }

      // Character sets
      let uppercase = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
      let lowercase = "abcdefghijklmnopqrstuvwxyz";
      let numbers = "0123456789";
      let symbols = "!@#$%^&*()_+-=[]{}|;:,.<>?";

      // Exclude similar characters if requested
      if (settings.excludeSimilar) {
        uppercase = uppercase.replace(/[IL]/g, "");
        lowercase = lowercase.replace(/[il]/g, "");
        numbers = numbers.replace(/[01]/g, "");
        symbols = symbols.replace(/[|]/g, "");
      }

      // Exclude ambiguous characters if requested
      if (settings.excludeAmbiguous) {
        uppercase = uppercase.replace(/[O]/g, "");
        lowercase = lowercase.replace(/[o]/g, "");
        numbers = numbers.replace(/[0]/g, "");
        symbols = symbols.replace(/[{}[\]()\/\\'"~,;.<>]/g, "");
      }

      // Build character set
      let charset = "";
      const requiredChars: string[] = [];

      if (settings.includeUppercase) {
        charset += uppercase;
        if (uppercase.length > 0) {
          requiredChars.push(uppercase[Math.floor(Math.random() * uppercase.length)]);
        }
      }

      if (settings.includeLowercase) {
        charset += lowercase;
        if (lowercase.length > 0) {
          requiredChars.push(lowercase[Math.floor(Math.random() * lowercase.length)]);
        }
      }

      if (settings.includeNumbers) {
        charset += numbers;
        if (numbers.length > 0) {
          requiredChars.push(numbers[Math.floor(Math.random() * numbers.length)]);
        }
      }

      if (settings.includeSymbols) {
        charset += symbols;
        if (symbols.length > 0) {
          requiredChars.push(symbols[Math.floor(Math.random() * symbols.length)]);
        }
      }

      if (charset.length === 0) {
        toast.error("No valid characters available with current settings");
        return;
      }

      // Generate password using crypto.getRandomValues for security
      const array = new Uint32Array(settings.length);
      crypto.getRandomValues(array);

      let password = "";

      // First, add required characters to ensure all types are included
      for (let i = 0; i < requiredChars.length && i < settings.length; i++) {
        password += requiredChars[i];
      }

      // Fill the rest with random characters
      for (let i = requiredChars.length; i < settings.length; i++) {
        password += charset[array[i] % charset.length];
      }

      // Shuffle the password to avoid predictable patterns
      const passwordArray = password.split("");
      for (let i = passwordArray.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [passwordArray[i], passwordArray[j]] = [passwordArray[j], passwordArray[i]];
      }

      const finalPassword = passwordArray.join("");
      setGeneratedPassword(finalPassword);
      onPasswordGenerated(finalPassword);
      
      toast.success("Password generated successfully!");
    } catch (error) {
      console.error("Error generating password:", error);
      toast.error("Failed to generate password. Please try again.");
    } finally {
      setIsGenerating(false);
    }
  };

  const copyToClipboard = async () => {
    try {
      await navigator.clipboard.writeText(generatedPassword);
      setCopied(true);
      toast.success("Password copied to clipboard!");
      setTimeout(() => setCopied(false), 2000);
    } catch (error) {
      console.error("Error copying to clipboard:", error);
      toast.error("Failed to copy password");
    }
  };

  const saveToLocalStorage = () => {
    try {
      const savedPasswords = JSON.parse(localStorage.getItem("generatedPasswords") || "[]");
      const newEntry = {
        password: generatedPassword,
        timestamp: new Date().toISOString(),
        settings: { ...settings }
      };
      savedPasswords.unshift(newEntry);
      
      // Keep only last 10 passwords
      if (savedPasswords.length > 10) {
        savedPasswords.splice(10);
      }
      
      localStorage.setItem("generatedPasswords", JSON.stringify(savedPasswords));
      toast.success("Password saved locally!");
    } catch (error) {
      console.error("Error saving password:", error);
      toast.error("Failed to save password");
    }
  };

  const updateSetting = <K extends keyof GeneratorSettings>(
    key: K,
    value: GeneratorSettings[K]
  ) => {
    setSettings(prev => ({ ...prev, [key]: value }));
  };

  const getStrengthEstimate = () => {
    let charsetSize = 0;
    if (settings.includeUppercase) charsetSize += 26;
    if (settings.includeLowercase) charsetSize += 26;
    if (settings.includeNumbers) charsetSize += 10;
    if (settings.includeSymbols) charsetSize += 32;

    if (settings.excludeSimilar) charsetSize -= 4;
    if (settings.excludeAmbiguous) charsetSize -= 8;

    const entropy = Math.log2(Math.pow(charsetSize, settings.length));
    
    if (entropy < 30) return { label: "Weak", color: "bg-red-500" };
    if (entropy < 50) return { label: "Medium", color: "bg-yellow-500" };
    if (entropy < 70) return { label: "Strong", color: "bg-blue-500" };
    return { label: "Very Strong", color: "bg-green-500" };
  };

  const strength = getStrengthEstimate();

  return (
    <Card className="border-0 shadow-xl bg-white/80 dark:bg-gray-800/80 backdrop-blur-sm">
      <CardHeader>
        <CardTitle className="flex items-center space-x-2">
          <Wand2 className="h-5 w-5 text-purple-500" />
          <span>Password Generator</span>
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-6">
        {/* Length Slider */}
        <div className="space-y-3">
          <div className="flex items-center justify-between">
            <Label className="text-sm font-medium">Password Length</Label>
            <Badge variant="outline" className="text-sm">
              {settings.length} characters
            </Badge>
          </div>
          <Slider
            value={[settings.length]}
            onValueChange={(value) => updateSetting("length", value[0])}
            min={4}
            max={50}
            step={1}
            className="w-full"
          />
          <div className="flex justify-between text-xs text-muted-foreground">
            <span>4</span>
            <span>50</span>
          </div>
        </div>

        <Separator />

        {/* Character Type Options */}
        <div className="space-y-4">
          <Label className="text-sm font-medium">Character Types</Label>
          
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
            <div className="flex items-center justify-between">
              <Label htmlFor="uppercase" className="text-sm">Uppercase (A-Z)</Label>
              <Switch
                id="uppercase"
                checked={settings.includeUppercase}
                onCheckedChange={(checked) => updateSetting("includeUppercase", checked)}
              />
            </div>

            <div className="flex items-center justify-between">
              <Label htmlFor="lowercase" className="text-sm">Lowercase (a-z)</Label>
              <Switch
                id="lowercase"
                checked={settings.includeLowercase}
                onCheckedChange={(checked) => updateSetting("includeLowercase", checked)}
              />
            </div>

            <div className="flex items-center justify-between">
              <Label htmlFor="numbers" className="text-sm">Numbers (0-9)</Label>
              <Switch
                id="numbers"
                checked={settings.includeNumbers}
                onCheckedChange={(checked) => updateSetting("includeNumbers", checked)}
              />
            </div>

            <div className="flex items-center justify-between">
              <Label htmlFor="symbols" className="text-sm">Symbols (!@#$)</Label>
              <Switch
                id="symbols"
                checked={settings.includeSymbols}
                onCheckedChange={(checked) => updateSetting("includeSymbols", checked)}
              />
            </div>
          </div>
        </div>

        <Separator />

        {/* Advanced Options */}
        <div className="space-y-4">
          <Label className="text-sm font-medium">Advanced Options</Label>
          
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <div>
                <Label htmlFor="excludeSimilar" className="text-sm">Exclude Similar</Label>
                <p className="text-xs text-muted-foreground">Avoid i, l, 1, L, o, 0, O</p>
              </div>
              <Switch
                id="excludeSimilar"
                checked={settings.excludeSimilar}
                onCheckedChange={(checked) => updateSetting("excludeSimilar", checked)}
              />
            </div>

            <div className="flex items-center justify-between">
              <div>
                <Label htmlFor="excludeAmbiguous" className="text-sm">Exclude Ambiguous</Label>
                <p className="text-xs text-muted-foreground">Avoid {`{ } [ ] ( ) / \\ ' " ~ , ; . < >`}</p>
              </div>
              <Switch
                id="excludeAmbiguous"
                checked={settings.excludeAmbiguous}
                onCheckedChange={(checked) => updateSetting("excludeAmbiguous", checked)}
              />
            </div>
          </div>
        </div>

        {/* Strength Indicator */}
        <div className="flex items-center justify-between p-3 bg-muted/50 rounded-lg">
          <span className="text-sm font-medium">Estimated Strength</span>
          <Badge className={`${strength.color} text-white border-0`}>
            {strength.label}
          </Badge>
        </div>

        {/* Generate Button */}
        <Button
          onClick={generatePassword}
          disabled={isGenerating}
          className="w-full h-12 text-lg bg-gradient-to-r from-purple-500 to-blue-500 hover:from-purple-600 hover:to-blue-600"
        >
          {isGenerating ? (
            <RefreshCw className="h-5 w-5 animate-spin mr-2" />
          ) : (
            <Wand2 className="h-5 w-5 mr-2" />
          )}
          Generate Password
        </Button>

        {/* Generated Password Display */}
        {generatedPassword && (
          <div className="space-y-3">
            <Label className="text-sm font-medium">Generated Password</Label>
            <div className="relative">
              <Input
                value={generatedPassword}
                readOnly
                className="pr-20 font-mono text-sm"
                onClick={(e) => e.currentTarget.select()}
              />
              <div className="absolute right-2 top-1/2 transform -translate-y-1/2 flex space-x-1">
                <Button
                  size="sm"
                  variant="ghost"
                  onClick={copyToClipboard}
                  className="h-8 w-8 p-0"
                >
                  {copied ? (
                    <Check className="h-4 w-4 text-green-500" />
                  ) : (
                    <Copy className="h-4 w-4" />
                  )}
                </Button>
              </div>
            </div>
            
            <div className="flex space-x-2">
              <Button
                variant="outline"
                size="sm"
                onClick={copyToClipboard}
                className="flex-1"
              >
                <Copy className="h-4 w-4 mr-2" />
                Copy
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={saveToLocalStorage}
                className="flex-1"
              >
                Save Locally
              </Button>
            </div>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
