"use client";

import { useEffect, useState } from "react";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Shield, AlertTriangle, CheckCircle, XCircle } from "lucide-react";
import { checkPwned } from "@/lib/pwned";

interface PasswordStrengthMeterProps {
  password: string;
}

interface StrengthResult {
  score: number;
  label: string;
  color: string;
  entropy: number;
  feedback: string[];
}

export function PasswordStrengthMeter({ password }: PasswordStrengthMeterProps) {
  const [strengthResult, setStrengthResult] = useState<StrengthResult | null>(null);
  const [isPwned, setIsPwned] = useState<boolean | null>(null);
  const [isCheckingPwned, setIsCheckingPwned] = useState(false);

  const calculatePasswordStrength = (password: string): StrengthResult => {
    try {
      if (!password) {
        return {
          score: 0,
          label: "No Password",
          color: "bg-gray-400",
          entropy: 0,
          feedback: ["Enter a password to begin analysis"]
        };
      }

      let score = 0;
      let entropy = 0;
      const feedback: string[] = [];

      // Character set analysis
      const hasLower = /[a-z]/.test(password);
      const hasUpper = /[A-Z]/.test(password);
      const hasNumbers = /\d/.test(password);
      const hasSymbols = /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/.test(password);
      const hasSpaces = /\s/.test(password);

      // Calculate character set size
      let charsetSize = 0;
      if (hasLower) charsetSize += 26;
      if (hasUpper) charsetSize += 26;
      if (hasNumbers) charsetSize += 10;
      if (hasSymbols) charsetSize += 32;
      if (hasSpaces) charsetSize += 1;

      // Calculate entropy: log2(charsetSize^length)
      if (charsetSize > 0) {
        entropy = Math.log2(Math.pow(charsetSize, password.length));
      }

      // Length scoring
      if (password.length >= 8) score += 20;
      if (password.length >= 12) score += 15;
      if (password.length >= 16) score += 15;
      else if (password.length < 8) feedback.push("Use at least 8 characters");

      // Character variety scoring
      if (hasLower) score += 10;
      else feedback.push("Add lowercase letters");

      if (hasUpper) score += 10;
      else feedback.push("Add uppercase letters");

      if (hasNumbers) score += 10;
      else feedback.push("Add numbers");

      if (hasSymbols) score += 15;
      else feedback.push("Add special characters (!@#$%^&*)");

      // Pattern detection
      const commonPatterns = [
        /123456/,
        /password/i,
        /qwerty/i,
        /abc123/i,
        /admin/i,
        /letmein/i,
        /welcome/i,
        /monkey/i,
        /dragon/i,
        /master/i
      ];

      const hasCommonPattern = commonPatterns.some(pattern => pattern.test(password));
      if (hasCommonPattern) {
        score -= 20;
        feedback.push("Avoid common words and patterns");
      }

      // Sequential characters
      const hasSequential = /(?:abc|bcd|cde|def|efg|fgh|ghi|hij|ijk|jkl|klm|lmn|mno|nop|opq|pqr|qrs|rst|stu|tuv|uvw|vwx|wxy|xyz|012|123|234|345|456|567|678|789)/i.test(password);
      if (hasSequential) {
        score -= 10;
        feedback.push("Avoid sequential characters");
      }

      // Repeated characters
      const hasRepeated = /(.)\1{2,}/.test(password);
      if (hasRepeated) {
        score -= 10;
        feedback.push("Avoid repeated characters");
      }

      // Ensure score is within bounds
      score = Math.max(0, Math.min(100, score));

      // Determine strength label and color
      let label: string;
      let color: string;

      if (score < 30) {
        label = "Very Weak";
        color = "bg-red-500";
      } else if (score < 50) {
        label = "Weak";
        color = "bg-orange-500";
      } else if (score < 70) {
        label = "Medium";
        color = "bg-yellow-500";
      } else if (score < 85) {
        label = "Strong";
        color = "bg-blue-500";
      } else {
        label = "Very Strong";
        color = "bg-green-500";
      }

      if (feedback.length === 0) {
        feedback.push("Excellent password strength!");
      }

      return {
        score,
        label,
        color,
        entropy: Math.round(entropy * 10) / 10,
        feedback
      };
    } catch (error) {
      console.error("Error calculating password strength:", error);
      return {
        score: 0,
        label: "Error",
        color: "bg-gray-400",
        entropy: 0,
        feedback: ["Unable to calculate strength"]
      };
    }
  };

  useEffect(() => {
    if (password) {
      const result = calculatePasswordStrength(password);
      setStrengthResult(result);

      // Check if password has been pwned
      const checkPassword = async () => {
        setIsCheckingPwned(true);
        try {
          const pwned = await checkPwned(password);
          setIsPwned(pwned);
        } catch (error) {
          console.error("Error checking pwned status:", error);
          setIsPwned(null);
        } finally {
          setIsCheckingPwned(false);
        }
      };

      checkPassword();
    } else {
      setStrengthResult(null);
      setIsPwned(null);
    }
  }, [password]);

  if (!strengthResult) {
    return null;
  }

  return (
    <Card className="border-0 shadow-lg">
      <CardHeader className="pb-3">
        <CardTitle className="flex items-center space-x-2 text-lg">
          <Shield className="h-5 w-5 text-blue-500" />
          <span>Password Strength Analysis</span>
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        {/* Strength Meter */}
        <div className="space-y-2">
          <div className="flex items-center justify-between">
            <span className="text-sm font-medium">Strength Score</span>
            <Badge 
              variant="secondary" 
              className={`${strengthResult.color} text-white border-0`}
            >
              {strengthResult.label}
            </Badge>
          </div>
          <Progress 
            value={strengthResult.score} 
            className="h-3 transition-all duration-500"
          />
          <div className="flex justify-between text-xs text-muted-foreground">
            <span>0%</span>
            <span>{strengthResult.score}%</span>
            <span>100%</span>
          </div>
        </div>

        {/* Entropy Display */}
        <div className="flex items-center justify-between p-3 bg-muted/50 rounded-lg">
          <div>
            <p className="text-sm font-medium">Password Entropy</p>
            <p className="text-xs text-muted-foreground">Measure of randomness</p>
          </div>
          <div className="text-right">
            <p className="text-lg font-bold text-blue-600">{strengthResult.entropy}</p>
            <p className="text-xs text-muted-foreground">bits</p>
          </div>
        </div>

        {/* Breach Check */}
        <div className="flex items-center justify-between p-3 bg-muted/50 rounded-lg">
          <div>
            <p className="text-sm font-medium">Breach Check</p>
            <p className="text-xs text-muted-foreground">Have I Been Pwned database</p>
          </div>
          <div className="flex items-center space-x-2">
            {isCheckingPwned ? (
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-500"></div>
            ) : isPwned === true ? (
              <>
                <XCircle className="h-4 w-4 text-red-500" />
                <span className="text-sm text-red-600 font-medium">Found in breaches</span>
              </>
            ) : isPwned === false ? (
              <>
                <CheckCircle className="h-4 w-4 text-green-500" />
                <span className="text-sm text-green-600 font-medium">Not found</span>
              </>
            ) : (
              <>
                <AlertTriangle className="h-4 w-4 text-yellow-500" />
                <span className="text-sm text-yellow-600 font-medium">Check failed</span>
              </>
            )}
          </div>
        </div>

        {/* Feedback */}
        {strengthResult.feedback.length > 0 && (
          <div className="space-y-2">
            <p className="text-sm font-medium">Recommendations:</p>
            <ul className="space-y-1">
              {strengthResult.feedback.map((item, index) => (
                <li key={index} className="text-sm text-muted-foreground flex items-start space-x-2">
                  <span className="text-blue-500 mt-1">•</span>
                  <span>{item}</span>
                </li>
              ))}
            </ul>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
