"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Copy, Check, Hash, Shield, Clock } from "lucide-react";
import { toast } from "sonner";

interface PasswordHashingDemoProps {
  password: string;
}

interface HashResult {
  algorithm: string;
  hash: string;
  time: number;
  iterations?: number;
}

export function PasswordHashingDemo({ password }: PasswordHashingDemoProps) {
  const [hashResults, setHashResults] = useState<HashResult[]>([]);
  const [isHashing, setIsHashing] = useState(false);
  const [copiedHash, setCopiedHash] = useState<string | null>(null);

  // SHA-256 hashing using Web Crypto API
  const hashSHA256 = async (text: string): Promise<HashResult> => {
    const startTime = performance.now();
    try {
      const encoder = new TextEncoder();
      const data = encoder.encode(text);
      const hashBuffer = await crypto.subtle.digest('SHA-256', data);
      const hashArray = Array.from(new Uint8Array(hashBuffer));
      const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
      const endTime = performance.now();
      
      return {
        algorithm: 'SHA-256',
        hash: hashHex,
        time: endTime - startTime
      };
    } catch (error) {
      console.error('Error hashing with SHA-256:', error);
      throw new Error('SHA-256 hashing failed');
    }
  };

  // SHA-512 hashing using Web Crypto API
  const hashSHA512 = async (text: string): Promise<HashResult> => {
    const startTime = performance.now();
    try {
      const encoder = new TextEncoder();
      const data = encoder.encode(text);
      const hashBuffer = await crypto.subtle.digest('SHA-512', data);
      const hashArray = Array.from(new Uint8Array(hashBuffer));
      const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
      const endTime = performance.now();
      
      return {
        algorithm: 'SHA-512',
        hash: hashHex,
        time: endTime - startTime
      };
    } catch (error) {
      console.error('Error hashing with SHA-512:', error);
      throw new Error('SHA-512 hashing failed');
    }
  };

  // Simple bcrypt simulation (for demonstration purposes)
  const simulateBcrypt = async (text: string): Promise<HashResult> => {
    const startTime = performance.now();
    try {
      // This is a simplified simulation of bcrypt
      // In a real application, you would use a proper bcrypt library
      const rounds = 12;
      const salt = 'demo_salt_' + Math.random().toString(36).substring(2, 15);
      
      // Simulate the computational cost of bcrypt
      let hash = text + salt;
      for (let i = 0; i < Math.pow(2, rounds); i++) {
        const encoder = new TextEncoder();
        const data = encoder.encode(hash);
        const hashBuffer = await crypto.subtle.digest('SHA-256', data);
        const hashArray = Array.from(new Uint8Array(hashBuffer));
        hash = hashArray.map(b => b.toString(16).padStart(2, '0')).join('').substring(0, 32);
        
        // Break early for demo purposes to avoid blocking the UI
        if (i > 1000) break;
      }
      
      const endTime = performance.now();
      const bcryptFormat = `$2b$${rounds.toString().padStart(2, '0')}$${salt.substring(0, 22)}${hash.substring(0, 31)}`;
      
      return {
        algorithm: 'bcrypt (simulated)',
        hash: bcryptFormat,
        time: endTime - startTime,
        iterations: Math.min(Math.pow(2, rounds), 1000)
      };
    } catch (error) {
      console.error('Error simulating bcrypt:', error);
      throw new Error('bcrypt simulation failed');
    }
  };

  // PBKDF2 hashing using Web Crypto API
  const hashPBKDF2 = async (text: string): Promise<HashResult> => {
    const startTime = performance.now();
    try {
      const encoder = new TextEncoder();
      const passwordBuffer = encoder.encode(text);
      const saltBuffer = encoder.encode('demo_salt_pbkdf2');
      
      const keyMaterial = await crypto.subtle.importKey(
        'raw',
        passwordBuffer,
        { name: 'PBKDF2' },
        false,
        ['deriveBits']
      );
      
      const iterations = 100000;
      const derivedBits = await crypto.subtle.deriveBits(
        {
          name: 'PBKDF2',
          salt: saltBuffer,
          iterations: iterations,
          hash: 'SHA-256'
        },
        keyMaterial,
        256
      );
      
      const hashArray = Array.from(new Uint8Array(derivedBits));
      const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
      const endTime = performance.now();
      
      return {
        algorithm: 'PBKDF2',
        hash: hashHex,
        time: endTime - startTime,
        iterations: iterations
      };
    } catch (error) {
      console.error('Error hashing with PBKDF2:', error);
      throw new Error('PBKDF2 hashing failed');
    }
  };

  const performHashing = async () => {
    if (!password) return;
    
    setIsHashing(true);
    try {
      const results: HashResult[] = [];
      
      // Hash with different algorithms
      results.push(await hashSHA256(password));
      results.push(await hashSHA512(password));
      results.push(await hashPBKDF2(password));
      results.push(await simulateBcrypt(password));
      
      setHashResults(results);
      toast.success('Password hashing completed!');
    } catch (error) {
      console.error('Error during hashing:', error);
      toast.error('Failed to hash password');
    } finally {
      setIsHashing(false);
    }
  };

  const copyToClipboard = async (hash: string, algorithm: string) => {
    try {
      await navigator.clipboard.writeText(hash);
      setCopiedHash(hash);
      toast.success(`${algorithm} hash copied to clipboard!`);
      setTimeout(() => setCopiedHash(null), 2000);
    } catch (error) {
      console.error('Error copying to clipboard:', error);
      toast.error('Failed to copy hash');
    }
  };

  useEffect(() => {
    if (password) {
      performHashing();
    } else {
      setHashResults([]);
    }
  }, [password]);

  if (!password) {
    return (
      <Card className="border-0 shadow-xl bg-white/80 dark:bg-gray-800/80 backdrop-blur-sm">
        <CardHeader>
          <CardTitle className="flex items-center space-x-2">
            <Hash className="h-5 w-5 text-green-500" />
            <span>Password Hashing Demo</span>
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-center py-8">
            <Hash className="h-12 w-12 text-muted-foreground mx-auto mb-4" />
            <p className="text-muted-foreground">Enter a password to see hashing demonstrations</p>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="border-0 shadow-xl bg-white/80 dark:bg-gray-800/80 backdrop-blur-sm">
      <CardHeader>
        <CardTitle className="flex items-center space-x-2">
          <Hash className="h-5 w-5 text-green-500" />
          <span>Password Hashing Demo</span>
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-6">
        {isHashing ? (
          <div className="text-center py-8">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-green-500 mx-auto mb-4"></div>
            <p className="text-muted-foreground">Hashing password with multiple algorithms...</p>
          </div>
        ) : (
          <Tabs defaultValue="overview" className="w-full">
            <TabsList className="grid w-full grid-cols-2">
              <TabsTrigger value="overview">Overview</TabsTrigger>
              <TabsTrigger value="details">Hash Details</TabsTrigger>
            </TabsList>
            
            <TabsContent value="overview" className="space-y-4">
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                {hashResults.map((result, index) => (
                  <div
                    key={index}
                    className="p-4 border rounded-lg bg-muted/50 space-y-2"
                  >
                    <div className="flex items-center justify-between">
                      <Badge variant="outline" className="text-xs">
                        {result.algorithm}
                      </Badge>
                      <div className="flex items-center space-x-1 text-xs text-muted-foreground">
                        <Clock className="h-3 w-3" />
                        <span>{result.time.toFixed(2)}ms</span>
                      </div>
                    </div>
                    <div className="text-xs font-mono break-all bg-background p-2 rounded border">
                      {result.hash.substring(0, 32)}...
                    </div>
                    {result.iterations && (
                      <div className="text-xs text-muted-foreground">
                        Iterations: {result.iterations.toLocaleString()}
                      </div>
                    )}
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => copyToClipboard(result.hash, result.algorithm)}
                      className="w-full"
                    >
                      {copiedHash === result.hash ? (
                        <Check className="h-3 w-3 mr-1" />
                      ) : (
                        <Copy className="h-3 w-3 mr-1" />
                      )}
                      Copy Hash
                    </Button>
                  </div>
                ))}
              </div>
              
              <div className="p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg border border-blue-200 dark:border-blue-800">
                <div className="flex items-start space-x-2">
                  <Shield className="h-5 w-5 text-blue-500 mt-0.5" />
                  <div>
                    <h4 className="text-sm font-medium text-blue-900 dark:text-blue-100">
                      Security Note
                    </h4>
                    <p className="text-xs text-blue-700 dark:text-blue-300 mt-1">
                      Different hashing algorithms serve different purposes. bcrypt and PBKDF2 are designed 
                      to be slow and are ideal for password storage, while SHA algorithms are fast and 
                      suitable for data integrity verification.
                    </p>
                  </div>
                </div>
              </div>
            </TabsContent>
            
            <TabsContent value="details" className="space-y-4">
              {hashResults.map((result, index) => (
                <div key={index} className="space-y-3">
                  <div className="flex items-center justify-between">
                    <h4 className="text-lg font-medium">{result.algorithm}</h4>
                    <Badge variant="outline">
                      {result.time.toFixed(2)}ms
                    </Badge>
                  </div>
                  
                  <div className="space-y-2">
                    <Label className="text-sm">Full Hash</Label>
                    <div className="relative">
                      <Input
                        value={result.hash}
                        readOnly
                        className="font-mono text-xs pr-12"
                        onClick={(e) => e.currentTarget.select()}
                      />
                      <Button
                        size="sm"
                        variant="ghost"
                        onClick={() => copyToClipboard(result.hash, result.algorithm)}
                        className="absolute right-2 top-1/2 transform -translate-y-1/2 h-6 w-6 p-0"
                      >
                        {copiedHash === result.hash ? (
                          <Check className="h-3 w-3 text-green-500" />
                        ) : (
                          <Copy className="h-3 w-3" />
                        )}
                      </Button>
                    </div>
                  </div>
                  
                  <div className="grid grid-cols-2 gap-4 text-sm">
                    <div>
                      <Label className="text-xs text-muted-foreground">Hash Length</Label>
                      <p className="font-mono">{result.hash.length} characters</p>
                    </div>
                    <div>
                      <Label className="text-xs text-muted-foreground">Processing Time</Label>
                      <p className="font-mono">{result.time.toFixed(3)}ms</p>
                    </div>
                    {result.iterations && (
                      <>
                        <div>
                          <Label className="text-xs text-muted-foreground">Iterations</Label>
                          <p className="font-mono">{result.iterations.toLocaleString()}</p>
                        </div>
                        <div>
                          <Label className="text-xs text-muted-foreground">Security Level</Label>
                          <p className="font-mono">High</p>
                        </div>
                      </>
                    )}
                  </div>
                  
                  {index < hashResults.length - 1 && <Separator />}
                </div>
              ))}
              
              <div className="mt-6 p-4 bg-yellow-50 dark:bg-yellow-900/20 rounded-lg border border-yellow-200 dark:border-yellow-800">
                <h4 className="text-sm font-medium text-yellow-900 dark:text-yellow-100 mb-2">
                  Algorithm Comparison
                </h4>
                <ul className="text-xs text-yellow-700 dark:text-yellow-300 space-y-1">
                  <li><strong>SHA-256/512:</strong> Fast, deterministic, good for data integrity</li>
                  <li><strong>PBKDF2:</strong> Slow by design, configurable iterations, password storage</li>
                  <li><strong>bcrypt:</strong> Adaptive cost, salt included, industry standard for passwords</li>
                </ul>
              </div>
            </TabsContent>
          </Tabs>
        )}
      </CardContent>
    </Card>
  );
}
