"use client";

import { useState } from "react";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Eye, EyeOff } from "lucide-react";
import { Button } from "@/components/ui/button";

interface PasswordInputProps {
  password: string;
  onPasswordChange: (password: string) => void;
}

export function PasswordInput({ password, onPasswordChange }: PasswordInputProps) {
  const [showPassword, setShowPassword] = useState(false);

  const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    try {
      const newPassword = e.target.value;
      onPasswordChange(newPassword);
    } catch (error) {
      console.error("Error handling password change:", error);
    }
  };

  return (
    <div className="space-y-2">
      <Label htmlFor="password" className="text-sm font-medium">
        Enter Password to Analyze
      </Label>
      <div className="relative">
        <Input
          id="password"
          type={showPassword ? "text" : "password"}
          value={password}
          onChange={handlePasswordChange}
          placeholder="Type your password here..."
          className="pr-12 text-lg h-12 transition-all duration-300 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
          autoComplete="new-password"
        />
        <Button
          type="button"
          variant="ghost"
          size="sm"
          className="absolute right-2 top-1/2 transform -translate-y-1/2 h-8 w-8 p-0"
          onClick={() => setShowPassword(!showPassword)}
        >
          {showPassword ? (
            <EyeOff className="h-4 w-4 text-muted-foreground" />
          ) : (
            <Eye className="h-4 w-4 text-muted-foreground" />
          )}
        </Button>
      </div>
      {password && (
        <p className="text-xs text-muted-foreground">
          Password length: {password.length} characters
        </p>
      )}
    </div>
  );
}
